                                <el-button @click.native.prevent="open2(scope.$index, films)" type="text" size="small">
                                    
                                </el-button>
                                <el-button type="text" size="small" @click="showEditFilm(scope.row.id)">
                                    
                                </el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-col>
            </el-row>
        </div>
        <el-dialog title="" :visible.sync="editFilmVisible" width="30%" :before-close="handleClose">
