    computed: {
        films () {
            return this.data.film.map(item => {
                if (item.hot) {
                    item.hot = ''
                } else {
                    item.hot = ''
                }

                return item
            })
        }
    },
    methods: {
        deleteFilm (film) {
            this.films.splice(this.films.indexOf(film), 1)
            this.$message({
                message: '',
                type: 'success'
            })
        },
        deleteRow (index, rows) {
            rows.splice(index, 1)
            this.$message({
                message: '',
                type: 'success'
            })
        },
        open2 (index, rows) {
            this.$confirm('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                rows.splice(index, 1)
                this.$message({
                    message: '',
                    type: 'success'
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                })
            })
        },
        addFilm: function () {
