<template>
    <div>
        <h1>Select </h1>
        <div>
            <h3></h3>
            <div>
                <el-select v-model="value" placeholder="">
                    <el-option v-for="(item, index) in options" :key="index" :label="item.label" :value="item.value">

                    </el-option>
                </el-select>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data () {
        return {
            options: [{
                value: '1',
                label: ''
            }, {
                value: '2',
                label: ''
            }, {
                value: '3',
                label: ''
            }, {
                value: '4',
                label: ''
            }, {
                value: '5',
                label: ''
            }],
            value: ''
        }
    }
}
</script>
<style scoped>
</style>



