<template>
    <div>
        <el-input-number v-model="num1" @change="handleChange" :min="1" :max="10" :step="2" controls-position="left" label=""></el-input-number>
        <el-row>
            <el-col :span="12">
                <el-select v-model="value" placeholder="">
                    <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                        <span style="float: left">{{ item.label }}</span>
                        <span style="float: right; color: #8492a6; font-size: 13px">{{ item.value }}</span>
                    </el-option>
                </el-select>
            </el-col>
        </el-row>
    </div>
</template>
<script>
export default {
    data () {
        return {
            num1: 2,
            options: [{
                value: '1',
                label: ''
            }, {
                value: '2',
                label: ''
            }, {
                value: '3',
                label: ''
            }, {
                value: '4',
                label: ''
            }, {
                value: '5',
                label: ''
            }],
            value: ''
        }
    },
    methods: {
        handleChange (value) {
            console.log(value)
        }
    }
}
</script>
<style scoped>
</style>



